import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import goods from '@/common/model/goods/index';

export default {
	components: {
		uniLoadMore,
		empty,
		WucTab,
		goods
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [],
			items:[],
			loadingType:'',
			ipage:0,
		};
	},
	onLoad(options){
		this.loadNav();
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		// console.log(options)
		// if(options.state){				
		// this.tabCurrentIndex = +options.state;
		// }
		//


		//
		// if(options.state == 0){
		// 	this.loadData()
		// }
		//
		
	},
	onReachBottom(){
		this.loadData();
	},
	methods: {
		loadNav(){
			this.$http.post('/app/page/nav', {nav_types:''}).then(res => {
				if(res.data.items&&res.data.items.items){
					this.navList.push(
						...res.data.items.items
					);
					this.loadData();
				}
				
			}).catch(err => {});
		},
		//获取订单列表
		loadData(source){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let cid = navItem.cid;
			
			if(source=='tabChange'){
				this.items = [];
				this.ipage  = 0;
			}
			
			this.loadingType = 'loading';
			
			this.$http.post('/cms/goods/list', {ipage:this.ipage,cid:cid,sort:'day_sales'}).then(res => {
				this.loadingType = 'noMore';
				if(res.data.items&&res.data.items){
					if(source=='tabChange'){
						this.items = res.data.items;
					}else{
						this.items.push(
							...res.data.items
						);
					}
					if(res.data.pager&&res.data.pager.ipage){
						this.ipage = parseInt(res.data.pager.ipage)+1;
					}
					if(res.data.items.length>=20){							
						this.loadingType = 'more';
					}
				}
				// this.loaded = true;
			}).catch(err => {});
		}, 
		//swiper 切换
		changeTab(current){
			this.tabCurrentIndex = current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		}
	},
}
